import React, { useState} from "react";
import styles from './styles.module.css';
import { useHistory, useLocation } from "react-router-dom";
import { useClipboard } from 'use-clipboard-copy';
import { FacebookShareButton, WhatsappShareButton } from "react-share";
import { FacebookIcon, WhatsappIcon } from "react-share";


export const MemeGenerated = () => {

    const [copied, setCopied] = useState(false);

    const clipboard = useClipboard();
    const history = useHistory();
    const location = useLocation();
    const url = new URLSearchParams(location.search).get('url');

    const copyLink =() => {
        clipboard.copy(url);
        setCopied(true);
    };
    return(
        <div className={styles.container}>
            <button onClick={() => history.push('/')} className={styles.home}>
                New Template
            </button>
            { url && <img alt="meme" src={url} /> }
            <button onClick={copyLink} className={styles.copy}>
             {copied ? 'Link copied!' : 'Copy link'}
            </button>
            <container>
                <segment>
                    <div>
                        <FacebookShareButton url={url}
                        quote={"Hey check this meme out"}>
                            <FacebookIcon logoFILLColor='white' square={true}></FacebookIcon>
                        </FacebookShareButton>
                        <WhatsappShareButton url={url}
                        title={"Hey check this meme out"}>
                            <WhatsappIcon logoFILLColor='white' square={true}></WhatsappIcon>
                        </WhatsappShareButton>
                    </div>
                </segment>
            </container>
            <h3 className='headline'>Created by@Ketan Singh</h3>
        </div>   
    );     
};